.pages-banner_2
    min-height: 418px
    overflow: hidden
    @media screen and ( max-width: 1024px )
        height: 60vh
        min-height: 60vh
    .swiper-container
        display: flex
        justify-content: center
        align-items: center
    .swiper-slide
        position: relative
        display: flex
        align-items: center
        justify-content: center
        .img
            height: auto
            img
                width: 100%
                height: 100%
                object-fit: cover
            @media screen and ( max-width: 1024px )
                height: 60vh
        .container
            position: absolute
            width: 100%
            &.top
                top: 26%
            .wrapper
                width: 100%
            .title
                color: $white
                font-size: r(62px)
                font-weight: 900
                text-transform: uppercase
                line-height: 1
                letter-spacing: 0.83px
                span
                    display: block
            .content
                max-width: 510px
                margin-top: 25px
                color: $white
                font-size: r(20px)
                font-weight: 400
                letter-spacing: 0.4px
                line-height: 1
                height: 43px
                overflow: hidden

    @media screen and ( max-width: 768px )
        height: 55vh
        min-height: 55vh
        .content
            font-size: r(60px) !important
        .swiper-slide
            .img
                height: 55vh
            .wrapper
                .title
                    font-size: 30px
                .content
                    max-width: 340px
                    height: 32px
                    font-size: 15px !important
    @media screen and ( max-width: 576px )
        height: 50vh
        min-height: 50vh
        .content
            font-size: r(55px) !important
        .swiper-slide
            .img
                height: 50vh
    @media screen and ( max-width: 375.98px )
        height: 45vh
        min-height: 45vh
        .content
            font-size: r(52px) !important
        .swiper-slide
            .img
                height: 45vh
