.memberunit
	&.main-section
		@media(min-width: 1280px)
			padding: 90px 0
	.wrap-sub-tab
		margin-top: 25px
		margin-bottom: 18px
	.tabslet-tab
		display: flex
		align-items: center
		justify-content: center
		border-bottom: 1px solid #f4c3c5
		padding-bottom: 8px
		margin-bottom: 50px
		@media screen and ( max-width: 660px )
			justify-content: space-around
			flex-wrap: wrap
		li
			list-style: none
			display: flex
			align-items: center
			@media screen and ( max-width: 660px )
				flex: 0 0 50%
				justify-content: center

			a
				color: $gray
				font-size: r(22px)
				text-align: center
				text-transform: uppercase
				font-weight: 400
				letter-spacing: 0.44px
				margin: 0 41px
				@media screen and ( max-width: 992px )
					font-size: 16px
				@media screen and ( max-width: 660px )
					margin: 0 10px
				@media screen and ( max-width: 500px )
					margin: 0 0px
				@media screen and ( max-width: 575px )
					font-size: r(20px)
			&+li
				&::before
					content: ''
					width: 1px
					height: 57px
					background-color: #f4c3c5
					display: inline-block
					@media screen and ( max-width: 660px )
						width: 0px
			&:first-child
				a
					margin-right: 54px
					@media screen and ( max-width: 660px )
						margin-right: 0

			&:last-child
				a
					margin-left: 54px
					@media screen and ( max-width: 660px )
						margin-left: 0

			&.active
				a
					text-shadow: 0.7px 0 0 #606060
					letter-spacing: 0.44px
					border-bottom: 2px solid $main
	.wrap-content
		.box-product
			.img
				a
					+img-ratio(275/490)
			.box-des
				height: 185px
				background-color: #f8f8f8
				margin-bottom: 20px
				padding: 20px
				padding-top: 10px
				.title
					color: $main
					font-size: r(16px)
					line-height: 1.2
					font-weight: 900
					text-align: left
					text-transform: uppercase
					letter-spacing: 0.16px
					margin-bottom: 10px
					display: block
					+rgtext(3)
				.list-items
					li
						color: $gray
						font-size: r(15px)
						font-weight: 400
						letter-spacing: 0.3px
						display: flex
						align-items: baseline
						line-height: 1.2
						&+li
							margin-top: 5px
						i
							font-size: 12px
							color: #999999
							padding-right: 8px
