.sabeco-contact
	.contact-list
		margin-top: 20px
	.right
		margin-top: 30px
	@media(min-width: 768px)
		.contact-list
			margin-top: 30px
		.left 
			padding-right: 30px
			position: relative
			&::before
				content: ''
				position: absolute
				top: 0
				right: -10px
				background: rgba($main, 0.28) 
				width: 1px 
				height: 100%
		.right 
			padding-left: 30px
			margin-top: 0
	@media(min-width: 1280px)
		padding: 60px 0 
		padding-bottom: 80px
		.left 
			padding-right: 47px
		.right 
			padding-left: 47px
		.contact-list
			margin-top: 40px
		.contact-item
			+ .contact-item
				margin-top: 40px
