.pages-product-list
    min-height: 570px
    margin-top: 50px
    &:last-child
        border-bottom: thin solid $main
    @media screen and ( max-width: 992px )
        margin-top: 0px
        &:nth-of-type(1)
            margin-top: 50px
    .row
        align-items: center
        .col-lg-4
            &:first-child
                margin-top: -55px
                @media screen and ( max-width: 992px )
                    margin-top: 0px
            @media screen and ( max-width: 992px )
                &:last-child
                    margin-top: 30px
    .beer-info
        display: flex
        align-items: flex-start
        .info-item
            display: flex
            align-items: flex-start
            flex-direction: column
            & + .info-item
                margin-left: 50px
            p
                &:first-child
                    color: $main
                    font-size: r(40px)
                    font-weight: 900
                    letter-spacing: 0.8px
                    position: relative
                    &::before
                        content: ''
                        position: absolute
                        top: 100%
                        left: 0
                        width: 40px
                        height: 2px
                        background: $main
                &:last-child
                    margin-top: 10px
                    color: $gray
                    font-size: r(18px)
                    font-weight: 400
                    letter-spacing: 0.36px
    .beer-title
        color: $main
        font-size: r(24px)
        font-weight: 700
        letter-spacing: 0.48px
        margin-top: 10px
        line-height: 1.3
        @media screen and ( min-width: 1024px )
            max-width: 390px
    .content
        color: $gray
        margin-top: 20px
        font-size: r(18px)
        line-height: 1.4
        font-weight: 400
        letter-spacing: 0.36px
        @media screen and ( min-width: 1024px )
            max-width: 380px
    .beer-img
        padding-right: 60px
        @media screen and ( max-width: 992px )
            padding: 0px 15px
        .img
            +img-ratio(330/328,unset)
    @media screen and ( max-width: 992px )
        .beer-img
            order: 2
            margin-top: 40px !important
        .content-wrapper
            order: 1
