.sabeco-careers-woking-culture-1
	.des
		max-width: 895px
		margin-left: auto
		margin-right: auto
.sabeco-careers-woking-culture-2
	background: #f3f3f3
	padding: 35px 0
	.news-item
		.image
			+img-scale(236/306)
		.caption
			.title 
				font-size: r(22px)
				font-weight: 700
				text-transform: uppercase
				line-height: 1.3
	.main-slide
		margin-top: 30px
		position: relative
		padding-left: 40px
		padding-right: 40px
		.main-button 
			margin-top: 0
			.button-prev, .button-next
				position: absolute
				top: 35%
				transform: translateY(-50%)
				z-index: 11
				opacity: 1
				transition: .2s ease-in-out all
				margin: 0
				img 
					width: 17px
					min-width: 17px
				&.swiper-button-disabled
					opacity: 0
			.button-prev
				right: 95%
			.button-next 
				left: 95%
		@media(min-width: 768px)
			margin-top: 35px
			padding-left: 0
			padding-right: 0
			.main-button 
				margin-top: 0
				.button-prev, .button-next
					top: 35%
					img 
						width: 24px
						min-width: 24px
				.button-prev
					right: 101%
				.button-next 
					left: 101%
		@media(min-width: 1024px)
			margin-top: 40px
		@media(min-width: 1280px)
			margin-top: 45px
			.main-button
				.button-prev
					right: 109%
				.button-next 
					left: 109%
	@media(min-width: 1280px)
		min-height: 622px
		padding-top: 65px
		padding-bottom: 60px
.sabeco-careers-woking-culture-3
	padding: 50px 0
	.box-content 
		.des 
			margin-top: 20px
			* + * 
				margin-top: 15px
		@media(min-width: 768px)
			.des 
				* + * 
					margin-top: 20px
	.image
		margin-top: 20px
		+img-scale(318/458)
		@media(min-width: 768px)
			margin-top: 0
	.recruitment-list
		.recruitment-item
			+ .recruitment-item
				margin-top: 30px
			.recruitment-title
				text-align: center
			.des
				text-align: center
			@media(min-width: 768px)
				+ .recruitment-item
					margin-top: 40px
				.recruitment-title
					text-align: left
				.des
					text-align: left
				&:nth-child(2n)
					flex-direction: row-reverse
			@media(min-width: 1024px)
				+ .recruitment-item
					margin-top: 50px
			@media(min-width: 1280px)
				+ .recruitment-item
					margin-top: 70px
				.box-content 
					margin-right: 30px
				.image
					margin-left: 30px
				&:nth-child(2n)
					.box-content 
						margin-right: 0
						margin-left: 30px
					.image
						margin-left: 0
						margin-right: 30px
			@media(min-width: 1440px)
				+ .recruitment-item
					margin-top: 100px
	@media(min-width: 1280px)
		padding-top: 100px
		padding-bottom: 105px
.careers-woking-culture-page
	.sabeco-home-6
		.box-content
			padding-right: 10px
		@media(max-width: 576px)
			min-height: 510px
			