.searchBreadcrumb
    height: 116px
    border: 1px solid #d22630
    background-color: $main
    display: flex
    align-items: center
    @media screen and ( max-width: 1280px )
        height: 100px
    @media screen and ( max-width: 768px )
        height: 90px
    @media screen and ( max-width: 576.98px )
        height: 80px
    @media screen and ( max-width: 375.98px )
        height: 65px
        .btn-search
            img
                width: 27px
                height: 27px
        input
            margin-left: 20px !important
    .container
        width: 100%
    .searchBreadcrumb-wrapper
        display: flex
        align-items: center
        justify-content: flex-start
        .btn-search
            border: 0
            background: transparent
        input
            margin-left: 45px
            color: $white
            font-size: r(22px)
            font-weight: 400
            letter-spacing: 0.44px
            border: 0
            background: transparent
            width: 100%
            &::placeholder
                color: $white
