.sabeco-popup-home 
	display: flex
	justify-content: center
	align-items: center
	width: 100%
	padding: 0 !important
	height: 100vh
	width: 100vw
	z-index: 9999999
	background: #ffffff
	position: fixed
	top: 0
	left: 0
	right: 0
	bottom: 0
	.fancybox-button
		display: none
	.main-popup
		min-height: 970px
		width: 100%
		padding: 20px 
		display: flex
		justify-content: center
		align-items: center
		flex-direction: column
		max-width: 1440px
		background-image: url("../img/bg-popup.png")
		> * 
			width: 100% 
		.title 
			font-size: r(32px)
			font-weight: 900
			text-align: center
			color: #ffffff
			text-transform: uppercase
			line-height: 1.3
			max-width: 550px
			margin-left: auto 
			margin-right: auto 
		.age-list
			display: flex
			justify-content: center
			align-items: center
			margin-top: r(57.5px)
			flex-wrap: wrap 
			li 
				display: flex
				justify-content: center
				align-items: center
				margin: 17.5px

				a 
					font-size: 20px
					display: flex
					justify-content: center
					align-items: center
					height: 47px
					min-width: 254px
					font-size: r(20px)
					font-weight: 700
					color: $main
					text-transform: uppercase
					padding: 5px 15px
					border-radius: 5px
					transition: .2s ease-in-out all
					background: #ffffff
					text-align: center
					position: relative
					&::before
						content: ''
						position: absolute
						top: 50%
						left: 50%
						transform: translate(-50%, -50%)
						background: none
						border: 1px solid $main
						opacity: 0
						pointer-events: none
						width: 100%
						height: 100%
						transition: .2s ease-in-out all
						border-radius: 5px
					&:hover
						&::before
							width: calc( 100% - 8px )
							height: calc( 100% - 8px )
							opacity: 1
		.des
			margin-top: r(70px)
			text-align: center
			max-width: 745px
			margin-left: auto 
			margin-right: auto 
			line-height: 1.3
			font-size: 16px
		@media(max-width: 576px)
			padding: 0 15px
			.title 
				font-size: 20px
				line-height: 1.2
			.age-list
				margin-top: 10px
				li 
					margin: 10px
					a 
						font-size: 17px
						height: 40px
			.des
				margin-top: 20px
				font-size: 13px
				line-height: 1.4
				p 
					font-size: 13px !important
					line-height: 1.4
