.main-title
	font-size: r(52px)
	font-weight: 900
	color: $main
	text-transform: uppercase
	line-height: 1
	&.center
		text-align: center
	&.text-white
		color: #ffffff
	@media(max-width: 1023px)
		font-size: 44px
	@media(max-width: 768px)
		font-size: 40px
	@media(max-width: 576px)
		font-size: 38px
.sabeco-title
	font-size: 24px
	font-weight: 900
	color: $main
	line-height: 1.1
	@media(min-width: 576px)
		font-size: 26px
	@media(min-width: 768px)
		font-size: 28px
	@media(min-width: 1024px)
		font-size: 32px
	@media(min-width: 1280px)
		font-size: 36px
.related-title
	font-size: 20px
	font-weight: 900
	color: $main
	line-height: 1.1
	@media(min-width: 576px)
		font-size: 24px
	@media(min-width: 768px)
		font-size: 26px
	@media(min-width: 1024px)
		font-size: 28px
	@media(min-width: 1280px)
		font-size: 30px
.block-title
	color: $main
	font-size: r(45px)
	font-weight: 900
	line-height: 1.1
	text-transform: uppercase
	margin-bottom: 42px
	@media screen and ( max-width: 1200px )
		margin-bottom: 35px
	@media screen and ( max-width: 768px )
		margin-bottom: 25px
	@media screen and ( max-width: 576.98px )
		margin-bottom: 55px
		text-align: center
.date
	opacity: 0.5
	color: $gray
	font-size: 16px
	font-weight: 400
	letter-spacing: 0.32px
	@media screen and ( max-width: 375.98px )
		font-size: r(18px)
	// Text style for "02/01/2019"
.main-title-child
	font-size: r(24px)
	font-weight: 900
	color: $main
	text-transform: uppercase
.recruitment-title
	font-size: 24px
	font-weight: 900
	color: $main
	line-height: 1.1
	text-transform: uppercase
	@media(min-width: 576px)
		font-size: 28px
	@media(min-width: 768px)
		font-size: 32px
	@media(min-width: 1024px)
		font-size: 36px
	@media(min-width: 1280px)
		font-size: 40px
	@media(min-width: 1440px)
		font-size: 45px

.title-special
	padding-top: 15px
	color: $main
	font-size: r(24px)
	font-weight: 700
	text-transform: uppercase
	letter-spacing: 0.24px
	margin-bottom: -20px
	@media screen and ( max-width: 375.98px )
		padding-top: 40px
