.pages-product-detail-sec-1
    &.main-section
        padding-bottom: 0px !important
    .main-title
        margin-bottom: 15px
    .head-content
        margin: 0 auto
        max-width: 410px
        p
            color: $gray
            font-size: r(20px)
            font-weight: 400
            text-align: center
            line-height: 1.5
            letter-spacing: 0.4px
    .viral-video
        margin-top: 30px
        .video
            position: relative
            cursor: pointer
            // &::before
            //     content: ''
            //     position: absolute
            //     z-index: 1
            //     top: 0
            //     bottom: 0
            //     left: 0
            //     right: 0
            //     background-color: #00000080
            //     pointer-events: none
            a.video-link
                +img-ratio(560/1000)
            a.play
                position: absolute
                left: 50%
                top: 50%
                z-index: 3
                transform: translate(-50%,-50%)
                pointer-events: none

.pages-product-detail-sec-2
    min-height: 614px
    &.main-section
        @media screen and ( min-width: 1280px )
            padding-bottom: 120px
    .row
        align-items: flex-end
        .col-lg-4
            @media screen and ( max-width: 992px )
                &:last-child
                    margin-top: 30px
    .beer-info
        display: flex
        align-items: flex-start
        .info-item
            display: flex
            align-items: flex-start
            flex-direction: column
            & + .info-item
                margin-left: 50px
            p
                &:first-child
                    color: $main
                    font-size: r(40px)
                    font-weight: 900
                    letter-spacing: 0.8px
                    position: relative
                    &::before
                        content: ''
                        position: absolute
                        top: 100%
                        left: 0
                        width: 40px
                        height: 2px
                        background: $main
                &:last-child
                    margin-top: 10px
                    color: $gray
                    font-size: r(18px)
                    font-weight: 400
                    letter-spacing: 0.36px
    .beer-title
        color: $main
        font-size: r(24px)
        font-weight: 700
        letter-spacing: 0.48px
        margin-top: 10px
        line-height: 1.3
        @media screen and ( min-width: 1024px )
            max-width: 390px
    .content
        color: $gray
        margin-top: 20px
        font-size: r(18px)
        line-height: 1.4
        font-weight: 400
        letter-spacing: 0.36px
        @media screen and ( min-width: 1024px )
            max-width: 380px
    .beer-img
        padding: 0px 75px
        @media screen and ( max-width: 992px )
            padding: 0px 15px
        .img
            +img-ratio(479/328)
            @media screen and ( max-width: 992px )
                padding-top: 90%
                img
                    object-fit: contain

.pages-product-detail-sec-3
    min-height: 532px
    &.main-section
        @media screen and ( min-width: 1280px )
            padding: 75px 0px
    .main-title
        color: white
        text-align: center
        margin-bottom: 25px
    .general
        display: flex
        justify-content: space-evenly
        align-items: flex-start
        @media screen and ( max-width: 1024px )
            flex-direction: column
            align-items: center
            justify-content: center
        .info
            &:last-child
                margin-top: 25px
            @media screen and ( min-width: 993px )
                max-width: 427px
                margin-left: 40px
                margin-right: 40px
                &:last-child
                    margin-top: 0px
            .info-wrapper
                text-align: center
                color: white
                opacity: 0.8
                font-weight: 400
                .title
                    font-size: r(24px)
                    font-weight: 700
                    letter-spacing: 0.48px
                    line-height: 1.3
                .content
                    font-weight: 400
                    letter-spacing: 0.4px
                    line-height: 1.2
                &:not(:last-child)
                    margin-bottom: 25px
