.sabeco-newsroom
	.news-item
		margin-top: 30px
		.image
			+img-scale(175/313)
		.caption
			.title 
				font-weight: 700
				line-height: 1.2
		&.news-item-top
			margin-top: 0
	@media(min-width: 768px)
		.news-item.news-item-top
			display: flex
			background: #f3f3f3
			.image
				padding-top: 0
				height: 217px
				width: 412px
				min-width: 412px
			.caption
				margin-top: 0
				width: calc( 100% - 217px )
				padding-left: 15px
				
				padding-top: 15px
				padding-right: 15px
				.title 
					+line(2)
				.des 
					+line(3)
					margin-top: 15px
					line-height: 1.3
				.button 
					margin-top: 15px
		.news-bottom-list
			margin: 0 -15px
			> *
				flex: 0 0 33.3333%
				max-width: 33.3333%
				padding: 0 15px
	@media(min-width: 1024px)
		.news-item.news-item-top
			.image
				width: 618px
				min-width: 618px
				height: 350px
			.caption
				width: calc( 100% - 618px )
				padding: 25px 30px
				.des
					+line(6)
					margin-top: 20px
				.button 
					margin-top: 20px
