.sabeco-careers-recruitment-detail
	.date-and-share
		margin-top: 10px
	.share-social
		.btn-share-facebook
			display: flex
			justify-content: center
			align-items: center
			width: 32px
			height: 32px
			background: $main 
			border-radius: 50%
			color: #ffffff
			transition: .2s ease-in-out all
			em 
				font-size: 18px
				line-height: 1
	.box-detail
		max-width: 530px
		@media(min-width: 768px)
			padding-right: 20px
			
	.table 
		margin-top: 20px
		padding-top: 20px
		border-top: 1px solid $main
		h2, h3, h4 
			color: $main
			font-weight: 700
			font-size: r(30px)
		table
			width: 100% 
			tr 
				border-bottom: 1px solid #e1e1e1
			td 
				padding: 10px 0
				font-size: r(20px)
				font-weight: 400
				vertical-align: top 
				&:first-child
					width: 25px
				&:nth-child(2)
					padding-left: 15px
				
		@media(min-width: 1024px)
			margin-top: 30px
			padding-top: 25px
	.full-content 
		padding-top: 30px
		h2, h3, h4 
			color: $main
			font-weight: 700
			font-size: r(30px)
	.contact-hr 
		margin-top: 30px
		.box-contact
			margin-top: 30px
			background-color: #f3f3f3
			border-left: 7px solid $main
			padding: 30px 20px
			.title 
				font-size: r(20px)
				font-weight: 900
				color: $main
				text-transform: uppercase
				line-height: 1.1
			.infomation 
				li 
					display: flex
					font-size: 16px
					font-weight: 400
					margin-top: 12px
					* 
						padding-left: 8px
						* 
							padding-left: 0
					em 
						width: 20px
						min-width: 20px
						line-height: 1
						padding-left: 0
					a 
						+hover-underline(95%, $main)
						&:hover 
							color: $main
		@media(min-width: 768px)
			margin-top: 0
			.box-contact
				margin-top: 40px
	.other-recruitment
		margin-top: 30px
		.job-filter-list 
			margin-top: 30px
			.job-filter-item
				margin-top: 20px
				background: #f3f3f3
				padding: 15px 20px
				border-bottom: none 
				.title 
					font-size: 16px
					margin-top: 5px
				.location
					li 
						font-size: 16px
						margin-top: 5px
						&:before 
							height: 17px
		@media(min-width: 1024px)
			margin-top: 35px
			.job-filter-list 
				margin-top: 40px
	@media(min-width: 1280px)
		padding-top: 50px
		padding-bottom: 40px
	@media(min-width: 1440px)
		padding-top: 100px
		padding-bottom: 80px
