.sabeco-home-3
	position: relative
	.main-title
		text-align: center
		@media(min-width: 768px)
			text-align: left 
	.main-slide
		margin-top: 20px
	.des 
		margin-top: 20px
		text-align: center
		* 
			line-height: 1.3
		* + * 
			margin-top: 15px
		h3 
			font-size: 21px
			font-weight: 400
			line-height: 1.2
		@media(min-width: 768px)
			text-align: left 
			
			h3 
				font-size: r(24px)
	.box-content
		@media(min-width: 768px)
			padding-bottom: 220px
	.main-slide
		@media(min-width: 768px)
			position: relative
			// padding-bottom: 100px
			
		// 	position: relative
		// 	> .row 
		// 		[class*="col-"]
		// 			position: static
	.main-slide-top 
		margin-top: 30px
		position: relative
		.image 
			display: flex
			justify-content: center
			align-items: center
		.main-button 
			display: none
		@media(min-width: 768px)
			.swiper-slide
				> .row 
					flex-direction: row-reverse
					[class*="col-"]
						position: static
			.main-button
				display: flex
				align-items: center
				position: absolute
				bottom: 220px
				left: 250px
				z-index: 111
				.button-prev, .button-next
					width: 30px
					height: 30px
					border-radius: 50%
					min-width: 30px
					border: 1px solid $main
					transition: .2s ease-in-out all
					display: flex
					justify-content: center
					align-items: center
					img 
						transition: .2s ease-in-out all
					&:hover 
						background: red 
						img 
							filter: brightness(0) invert(1)
				.button-next
					margin-left: 25px
		@media(min-width: 1024px)
			.main-button
				left: 370px
				bottom: 225px
		@media(min-width: 1280px)
			.main-button
				left: 325px
			.col-xl-image
				flex: 0 0 57.3529411765%
				max-width: 57.3529411765%
				.image
					margin-left: 60px
			.col-xl-content 
				flex: 0 0 42.6470588235%
				max-width: 42.6470588235%
	.main-slide-thumbs 
		position: relative
		padding-left: 20px
		padding-right: 20px
		.image
			height: 120px
			width: 100%
			display: flex
			justify-content: center
			align-items: center
			overflow: hidden
			img 
				max-width: 100%
				max-height: 100%
		.button-prev, .button-next
			position: absolute
			top: 50%
			transform: translateY(-50%)
			z-index: 11
			img 
				height: 24px
		.button-prev
			left: 0
		.button-next
			right: 0
		@media(min-width: 768px)
			position: absolute
			bottom: 0
			left: 0 
			width: 100%
			z-index: 11
			background: #ffffff
			padding: 0
			max-width: 340px
			.image
				height: 160px
			.button-prev, .button-next, .main-button 
				display: none
		@media(min-width: 1024px)
			max-width: 400px
		@media(min-width: 1280px)
			max-width: 420px
