.banner-home 
	position: relative
	.image
		height: 175px
		width: 100%
		overflow: hidden
		display: none
		img 
			width: 100%
			height: 100%
			object-fit: cover
		@media(min-width: 576px)
			display: block
			height: 0
			+img-scale(665/1440)
		@media(min-width: 1024px)
			height: 475px
			padding-top: 0
		@media(min-width: 1280px)
			height: 665px
	.image-mobile 
		@media(min-width: 576px)
			display: none
	.content
		position: absolute
		bottom: 69px
		z-index: 11
		width: 100%
		@media(min-width: 768px)
			position: absolute
			top: 40%
			transform: translateY(-50%)
			left: 0
			width: 100%
			z-index: 11
			bottom: auto 
		@media(min-width: 1280px)
			top: 35%
		.title 
			font-size: 42px
			font-weight: 900
			text-transform: uppercase
			line-height: 1
			margin-top: 15px
			text-align: center
			color: #ffffff
			@media(min-width: 576px)
				font-size: 48px
			@media(min-width: 768px)
				font-size: 56px
				color: #ffffff
				margin-top: 0
				text-align: left
			@media(min-width: 1024px)
				font-size: 60px
			@media(min-width: 1280px)
				font-size: 64px
		.des 
			font-size: 15px
			font-weight: 400
			margin-top: 15px
			max-width: 495px
			color: #ffffff
			text-align: center
			line-height: 1.3
			@media(min-width: 576px)
				font-size: r(20px)
			@media(min-width: 768px)
				color: #ffffff
				text-align: left 
				line-height: 1.5
				margin-top: 20px
		.button
			@media(max-width: 576px)
				margin-top: 25px
	.main-button 
		display: none
		@media(min-width: 1024px)
			display: block
			.button-prev, .button-next
				position: absolute
				top: 50%
				transform: translateY(-50%)
				z-index: 11
				img 
					filter: brightness(0) invert(1)
					transition: .2s ease-in-out all
				&:hover 
					img 
						filter: brightness(100%)
			.button-prev
				left: 10px
			.button-next
				right: 10px
		@media(min-width: 1280px)
			.button-prev
				left: 80px
			.button-next
				right: 80px
	.main-page
		display: flex
		justify-content: center
		align-items: center
		position: absolute
		bottom: 28px
		left: 50%
		transform: translateX(-50%)
		z-index: 11
		width: 100%
		.swiper-pagination
			display: flex
			justify-content: center
			align-items: center
			// margin-top: 10px
			position: relative
			.swiper-pagination-bullet
				width: 8px
				height: 8px
				opacity: 1
				margin: 5px 
				background: #ffffff
				&.swiper-pagination-bullet-active
					background: $main
		@media(min-width: 768px)
			
			.swiper-pagination
				.swiper-pagination-bullet
					background: #ffffff
		@media(min-width: 1024px)
			display: none
