.sabeco-home-1
	display: flex
	justify-content: center
	align-items: flex-end
	min-height: 310px
	.main-title
		text-align: center
	.box-content
		text-align: center
		@media(min-width: 768px)
			padding-left: 50px
		@media(min-width: 1024px)
			padding-left: 60px
		@media(min-width: 1280px)
			padding-left: 80px
	.des 
		margin-top: 20px
		line-height: 1.2
	@media(min-width: 768px)
		min-height: 440px
		align-items: center
		.main-title, .box-content
			text-align: left
.sabeco-home-1
	@media(max-width: 576px)
		padding-bottom: 10px
