@media screen and ( max-width: 575px )
	.des
		p
			font-size: 15px!important
			font-weight: 400
	.button
		.btn-view-more
			font-size: 18px
			padding: 0px!important
.overview-1
	border-bottom: 1px solid #f4c3c5
	.des
		max-width: 779px
		margin: 0 auto
		text-align: center
		p
			line-height: 1.2
	@media screen and ( min-width:  1280px)
		padding-top: 80 / 16 * 1rem
		padding-bottom: 67 / 16 * 1rem
[class*="overview-"]
	.btn-view-more
		@media screen and ( max-width: 575px )
			height: 48px
	@media screen and ( max-width: 575px )
		.button
			width: 100%
			padding: 0 10px
		.btn-view-more
			width: 100%

.overview-2
	padding-top: 55 / 16 * 1rem
	.main-title
		font-size: r(52px)
		line-height: 1.2
		text-align: center
		max-width: 790px
		margin: 0 auto
	.swiper-wrapper
		align-items: baseline
	.wrap-slide
		position: relative
		.swiper-slide
			@media screen and ( max-width: 460px )
				opacity: 0.5
		.swiper-slide-active
			opacity: 1

		.image
			display: flex
			justify-content: center
			align-items: baseline
		.wrap-text
			.title-name
				color: $main
				font-size: 20px
				font-weight: 700
				text-align: center
				letter-spacing: 0.4px
				margin-top: 26px
			p
				color: $gray
				font-size: 20px
				font-weight: 400
				text-align: center
				letter-spacing: 0.4px
				line-height: 1.2
		.main-button
			.button-prev, .button-next
				position: absolute
				top: 41%
				transform: translateY(-50%)
				cursor: pointer
				z-index: 500
			.button-prev
				left: -107px
				@media screen and ( max-width: 1200px )
					left: -40px
				@media screen and ( max-width: 768px )
					left: -20px
				@media screen and ( max-width: 575px )
					left: 0
				@media screen and ( max-width: 460px )
					left: 65px
					width: 17px
					height: 31px
			.button-next
				right: -107px
				@media screen and ( max-width: 1200px )
					right: -40px
				@media screen and ( max-width: 768px )
					right: -20px
				@media screen and ( max-width: 575px )
					right: 0
				@media screen and ( max-width: 460px )
					right: 65px
					width: 17px
					height: 31px
	.wrap-sub-tab
		margin-top: 25px
		margin-bottom: 18px
		.tabslet-tab
			display: flex
			align-items: center
			justify-content: center
			border-bottom: 1px solid #f4c3c5
			padding-bottom: 18px
			margin-bottom: 50px
			@media screen and ( max-width: 660px )
				justify-content: space-around
			@media screen and ( max-width: 575px )
				flex-wrap: wrap

			li
				list-style: none
				display: flex
				align-items: center
				a
					color: $gray
					font-size: r(22px)
					text-align: center
					text-transform: uppercase
					font-weight: 400
					letter-spacing: 0.44px
					margin: 0 41px
					@media screen and ( max-width: 660px )
						margin: 0 10px
					@media screen and ( max-width: 500px )
						margin: 0 0px
					@media screen and ( max-width: 575px )
						font-size: r(20px)
				&+li
					&::before
						content: ''
						width: 1px
						height: 57px
						background-color: #f4c3c5
						display: inline-block
						@media screen and ( max-width: 660px )
							width: 0px
				&:first-child
					a
						margin-right: 54px
						@media screen and ( max-width: 660px )
							margin-right: 0

				&:last-child
					a
						margin-left: 54px
						@media screen and ( max-width: 660px )
							margin-left: 0

				&.active
					a
						text-shadow: 0.7px 0 0 #606060
						letter-spacing: 0.44px
						border-bottom: 2px solid $main

	.tabslet-content
		display: none
		&.active
			display: block
.overview-3
	min-height: 532px
	display: flex
	align-items: center
	justify-content: center
	@media screen and ( max-width: 575px )
		align-items: baseline
		padding-top: 50px
		background-position: bottom left!important

	.wrap-text
		padding-left: r(74px)
		@media screen and ( max-width: 768px )
			padding-left: 0
		@media screen and ( max-width: 575px )
			display: flex
			flex-direction: column
			justify-content: center
			align-items: center
		.main-title
			margin-bottom: 10px
			line-height: 1.3
			@media screen and ( max-width: 575px )
				font-size: r(50px)
				text-align: center
			@media screen and ( max-width: 768px )
				font-size: 31px

		.des
			p
				@media screen and ( max-width: 575px )
					text-align: center
					max-width: 80%
					margin: 0 auto
		.button
			.btn-view-more
				img
					padding-top: 2px
				@media screen and ( min-width: 1024px )
					min-width: 239px
				@media screen and ( max-width: 575px )
					width: 100%

			@media screen and ( max-width: 575px )
				width: 100%
				padding: 0 10px

.overview-4
	min-height: 534px
	display: flex
	align-items: center
	justify-content: center
	@media screen and ( max-width: 575px )
		align-items: baseline
		padding-top: 50px
		background-position: bottom right!important
		background-size: 189%!important
		background-position-x: 100%
		background-repeat: no-repeat
		min-height: 636px
	.wrap-text
		@media screen and ( min-width: 1024px )
			padding-right: 108px
		@media screen and ( max-width: 768px )
			margin-left: -16%
		@media screen and ( max-width: 575px)
			display: flex
			flex-direction: column
			margin-left: 0
			align-items: center
			justify-content: center
		.des
			p
				@media screen and ( max-width: 768px )
					max-width: 400px
				@media screen and ( max-width: 575px )
					max-width: 90%
					margin: 0 auto
					text-align: center

	.main-title
		line-height: 1.3
		margin-bottom: 10px
		@media screen and ( max-width: 575px )
			font-size: r(48px)
			text-align: center
			br
				display: inline
	.button
		@media screen and ( max-width: 575px )
			width: 100%
			padding: 0 10px

	.btn-view-more
		padding: 5px 22px
		@media screen and ( max-width: 575px )
			width: 100%

.overview-wrap-5
	padding-top: r(60px)
	@media screen and ( max-width: 575px )
		padding-top: r(60px)
	.overview-5
		&:nth-of-type(2n-1)
			.img
				img
					padding-right: 42px
					@media screen and ( max-width: 1023px )
						padding-right: 0

		&:nth-of-type(2n)
			padding-bottom: 50px
			.img
				img
					padding-left: 42px
					@media screen and ( max-width: 1023px )
						padding-left: 0
			@media screen and ( max-width: 1023px )
				.col-lg-4
					&:nth-of-type(2n-1)
						order: 2
					&:nth-of-type(2n)
						order: 1
.overview-5
	@media screen and ( max-width: 575px )
		padding-top: 50px
		padding-bottom: 0
	.pr-0
		padding-right: 0
		padding-left: 0
		@media screen and ( max-width: 1023px )
			padding-right: 10px
			padding-left: 10px
	.pl-0
		padding-left: 0
		padding-right: 0
		@media screen and ( max-width: 1023px)
			padding-left: 10px
			padding-right: 10px
	.img
		height: 482px
		background-color: #f8f8f8
		@media screen and ( max-width: 768px )
			height: auto
		@media screen and ( max-width: 575px )
			height: 300px
		@media screen and ( max-width: 375px )
			height: 220px
		img
			width: 100%
			height: 100%
			object-fit: cover
	.wrap-text
		padding-left: 45px
		width: 100%
		height: 482px
		background-color: #f8f8f8
		padding-top: r(33px)
		padding-right: r( 68px )
		@media screen and ( max-width: 1023px )
			height: auto
			padding-bottom: 30px
		@media screen and ( max-width: 575px )
			background-color: #fff
			display: flex
			align-items: center
			flex-direction: column
			justify-content: center
			padding-left: 0
			padding-right: 0
			padding-bottom: 0


		.main-title
			line-height: 1.2
			margin-bottom: 14px
			@media screen and ( max-width: 575px )
				font-size: r(48px)
				text-align: center
		.des p
			line-height: 1.2
			margin-bottom: 57px
			@media screen and ( max-width: 575px )
				padding: 0 10px
				text-align: center
				margin-bottom: 12px
		.button
			@media screen and ( max-width: 575px )
				width: 100%
				padding: 0 10px
		.btn-view-more
			min-width: 144px
			@media screen and ( max-width: 575px )
				width: 100%

