.distribution-oversea
	padding-top: r(100px)
	.menu-list-tablets
		// border-bottom: 0 solid #000
	.main-title
		margin-bottom: 41px
	@media screen and ( min-width: 1280px )
		.menu-list-tablets
			li
				margin-bottom: 25px
	.menu-list-tablets
		li
			&::before
				@media screen and ( min-width: 768px )
					right: -68%
				@media screen and ( min-width: 1280px )
					right: -165px

.db-oversea-1
	@media screen and ( max-width: 575px )
		padding-top: 30px!important
	.main-title
		@media screen and ( min-width: 1280px )
			font-size: r(30px)
			text-transform: none
			line-height: 1.2
			margin-bottom: 10px!important
		@media screen and ( max-width: 575px )
			margin-bottom: 10px!important

	.container
		width: 100%
	.wrap-text
		@media screen and ( max-width: 768px )
			text-align: left!important
		strong
			text-transform: uppercase
			color: $white
			font-size: r(16px)
			font-weight: 700
			text-transform: uppercase
			letter-spacing: 0.16px
			margin-bottom: 8px
			display: block
			@media screen and ( max-width: 575px )
				font-size: 20px
		li
			color: $white
			font-size: r(15px)
			letter-spacing: 0.3px
			display: flex
			align-items: baseline
			&+li
				margin-top: 7px
			i
				font-size: 12px
				margin-right: 8px
.db-oversea-2
	border-bottom: 1px solid rgba($main, 0.28)
	.wrap-mission-1
		padding-bottom: 100px
		@media screen and ( max-width: 992px )
			padding-bottom: 30px
		@media screen and ( max-width: 992px )
			margin-bottom: 30px
		.box-text
			background-color: #fff!important
			padding: 50px 20px 0px 2px!important
			@media screen and ( max-width: 768px )
				padding:20px 0px!important
			@media screen and ( max-width: 575px )
				padding-left: 0px!important
				padding-right: 0px!important
			.main-title
				font-size: r(30px)!important
				text-transform: none
				line-height: 1.2
				margin-bottom: 20px!important
			.des
				max-width: 100%!important
				@media screen and ( max-width: 768px )
					text-align: left!important
					display: block
					width: 100%
				p
					margin-bottom: 0!important
					line-height: 1.3

.db-address
	min-height: 215px
	padding: 40px 0px
	&:nth-of-type(2n-1)
		background-color: #fff
	&:nth-of-type(2n)
		background-color: $bg_color
	.box-left
		padding-right: 50px
	.title
		color: $main
		font-size: 24px
		font-weight: 700
		line-height: 32px
		text-align: left
		letter-spacing: 0.24px
		margin-bottom: 10px
	.list-address
		li
			display: flex
			align-items: baseline
			span
				color: $gray
				font-size: 16px
				font-weight: 400
				line-height: 24px
				text-align: left
				letter-spacing: 0.32px
			i
				color: $main
				font-size: 12px
				margin-right: 8px
	.title-select
		display: flex
		align-items: center
		color: $gray
		font-size: 18px
		font-weight: 700
		text-align: left
		letter-spacing: 0.18px
		width: 100%
		justify-content: space-between
		cursor: pointer
		&.active
			color: $main
	.wrap-select
		display: flex
		justify-content: space-between
		flex-wrap: wrap
		@media screen and ( max-width: 992px )
			margin-top: 30px
	.select-item
		flex: 0 0 285px
		margin-bottom: 37px
		border-bottom: 1px solid #e1e1e1
		padding-bottom: 13px
		height: fit-content
		@media screen and ( max-width: 1200px )
			flex: 0 0 250px
		@media screen and ( max-width: 1024px )
			flex: 0 0 48%
		@media screen and ( max-width: 575px )
			flex: 0 0 100%

	.content
		padding-top: 5px
		display: none
		li
			span
				font-size: 15px

