.sabeco-newsroom-detail
	.box-detail
		.date-and-share
			margin-top: 10px
		.share-social
			.btn-share-facebook
				display: flex
				justify-content: center
				align-items: center
				width: 32px
				height: 32px
				background: $main 
				border-radius: 50%
				color: #ffffff
				transition: .2s ease-in-out all
				em 
					font-size: 18px
					line-height: 1
		.full-content 
			margin-top: 20px
			border-top: 1px solid $main
			padding-top: 20px
			padding-bottom: 20px
			font-size: 16px
			color: $gray
			* 
				font-size: 16px
				color: $gray
				margin-bottom: 15px
				+ * 
					margin-top: 0
				&:last-child
					margin-bottom: 0
			h2, h3, h4, h5, 
				font-size: r(24px)
				font-weight: 700
				color: $red

			@media(min-width: 1024px)
				padding-top: 25px
				padding-bottom: 25px
				* 
					margin-bottom: 20px
			@media(min-width: 1280px)
				padding-bottom: 35px
			@media(min-width: 1440px)
				padding-bottom: 55px
	.news-related 
		border-top: 1px solid $main
		padding-top: 20px
		.main-slide
			position: relative
			margin-top: 20px
		.main-button 
			display: flex
			justify-content: center
			align-items: center
			margin-top: 20px
			.button-prev, .button-next
				display: flex
				justify-content: center
				align-items: center
				cursor: pointer
				margin: 0 10px
				&.swiper-button-disabled
					opacity: 0
					pointer-events: none
		.news-item
			.caption 
				margin-top: 15px
				.title 
					margin-top: 5px
					font-size: r(18px)
					font-weight: 700
					color: $gray
					line-height: 1.2 
			&:hover 
				.caption 
					.title 
						color: $main
		@media(min-width: 768px)
			.main-button 
				margin-top: 0
				.button-prev, .button-next
					position: absolute
					top: 35%
					transform: translateY(-50%)
					z-index: 11
					margin: 0
				.button-prev
					left: -40px
				.button-next
					right: -40px
		@media(min-width: 1280px)
			padding-top: 30px
			.main-slide
				margin-top: 25px
		@media(min-width: 1440px)
			padding-top: 40px
			.main-button 
				.button-prev
					left: -100px
				.button-next
					right: -100px
	@media(min-width: 1440px)
		padding-bottom: 70px
