.sabeco-careers-recruitment
	padding-top: 30px
	padding-bottom: 0
	.form-wrap
		display: flex
		flex-wrap: wrap 
		margin: 0 -10px
		> *
			flex: 0 0 100%
			width: 100%
			max-width: 100%
			padding: 0 10px
		.form-group
			> * 
				width: 100%
				height: 40px
				border-radius: 5px
				background-color: #ffffff
				padding: 5px 15px
				font-size: 16px
				font-weight: 400
				color: $gray
				text-transform: uppercase
				border: 1px solid $gray
				margin-top: 20px
			&.form-input 
				input 
					text-transform: none 
					&::placeholder 
						font-style: italic
						font-size: 14px
			&.form-select 
				flex: 0 0 50%
				max-width: 50%
			&.form-submit 
				button 
					background-color: $main 
					border-color: $main
					color: #ffffff
					font-size: 16px
					font-weight: 700
					text-transform: uppercase
					display: flex
					align-items: center
					justify-content: center
					text-align: center
					position: relative
					&::before
						content: ''
						position: absolute
						top: 50%
						transform: translate(-50%, -50%)
						left: 50%
						background: none 
						width: 100%
						height: 100%
						opacity: 0
						border: 1px solid #ffffff
						transition: .2s ease-in-out all
						border-radius: 5px
						pointer-events: none 
					&:hover 
						&::before
							width: calc(100% - 8px)
							height: calc(100% - 8px)
							opacity: 1 

					img 
						padding-left: 15px
		@media(min-width: 1024px)
			.form-group
				&.form-input
					flex: 0 0 38.235294118%
					max-width: 38.235294118%
				&.form-select 
					flex: 0 0 24.019607843%
					max-width: 24.019607843%
				&.form-submit 
					flex: 0 0 13.235294118%
					max-width: 13.235294118%
	.job-filter
		margin-top: 35px
		max-width: 510px
		.job-found
			font-size: r(22px)
			font-weight: 700
			color: $main
		.job-filter-item 
			margin-top: 20px
		@media(min-width: 768px)
			margin-top: 40px
			.job-filter-item 
				margin-top: 30px
		@media(min-width: 1280px)
			margin-top: 50px
	.box-submit-cv
		background: #f3f3f3
		padding: 40px 20px
		text-align: center
		margin-top: 50px
		position: relative
		&::before
			content: ''
			position: absolute
			top: 0
			left: -20px
			background: #f3f3f3
			width: 100%
			height: 100%
			z-index: -1
		&::after 
			content: ''
			position: absolute
			top: 0
			right: -20px
			background: #f3f3f3
			width: 100%
			height: 100%
			z-index: -1
		.content 
			font-size: r(20px)
			font-weight: 400
			color: $gray
			line-height: 1.3
		.button 
			display: flex
			justify-content: center
			align-items: center
		@media(min-width: 576px)
			&::before, &::after
				display: none
		@media(min-width: 1280px)
			padding: 50px 35px
			padding-top: 40px
			.button 
				margin-top: 35px
	@media(min-width: 576px)
		padding-bottom: 40px
	@media(min-width: 1280px)
		padding-bottom: 50px
	@media(min-width: 1440px)
		padding-bottom: 90px
