.pride-vietnamese
	padding-top: 40px
	.main-title
		font-size: r(30px)
		max-width: 717px
		margin: 0 auto
		text-transform: none
		line-height: 1.3
		padding-top: 48px
	.content
		column-count: 3
		column-gap: 28px
		margin-top: 40px
		margin-bottom: 90px
		@media screen and ( max-width: 768px )
			column-count: 2
		@media screen and ( max-width: 575px )
			column-count: 1
		p
			color: $gray
			font-size: r(20px)
			font-weight: 400
			text-align: justify
			letter-spacing: 0.4px
			line-height: 1.1
			margin-bottom: 25px
			img
				width: 100%
				
