.pages-FAQ
    border-bottom: thin solid $main
    &.main-section
        @media screen and ( min-width: 1024px )
            padding-top: 85px
    @media screen and ( min-width:1280px  )
        .left-side
            padding-right: 0px
            margin-right: -10px
        .right-side
            padding: 0px
    .left-side
        .faq-list
            @media screen and ( min-width: 993px )
                max-width: 510px
            .faq-item
                display: flex
                align-items: flex-start
                position: relative
                padding-top: 25px
                padding-bottom: 15px
                border-bottom: thin solid $main
                &:hover
                    .faq-ques
                        .question
                            color: $main
                &.active
                    .faq-id, .faq-ques
                        color: $main
                        &::before
                            background-color: $main
                    .faq-ques
                        .question
                            font-weight: 700
                            color: $main
                    .down_arrow
                        transform: rotate(180deg)
                .faq-id
                    color: $gray
                    font-size: r(45px)
                    font-weight: 900
                    text-transform: uppercase
                    letter-spacing: 0.45px
                    padding-right: 18px
                    position: relative
                    transition: .3s all  ease-in-out
                    &::before
                        position: absolute
                        content: ''
                        top: 50%
                        transform: translateY(-50%)
                        right: 0px
                        width: 7px
                        height: 40px
                        background-color: $gray
                .faq-ques
                    padding-left: 20px
                    margin-top: 6px
                    .question
                        max-width: 392px
                        color: $gray
                        font-size: r(20px)
                        font-weight: 400
                        letter-spacing: 0.4px
                        line-height: 1.3
                        transition: .3s all  ease-in-out
                        cursor: pointer
                    .content
                        display: none
                        color: $gray
                        font-size: r(16px)
                        font-weight: 400
                        letter-spacing: 0.32px
                        line-height: 1.3
                        margin-top: 10px
                .down_arrow
                    position: absolute
                    right: 0
                    top: 31px
                    transition: .3s all  ease-in-out
    .right-side
        @media screen and ( max-width: 992px )
            margin-top: 20px !important
        .wrap-form
            padding: 30px
            background-color: $bg_color
            .form-group
                position: relative
                margin-bottom: 15px
                label
                    position: absolute
                    top: 4px
                    left: 20px
                    em
                        color: #999999
                        font-size: r(18px)
                input,textarea
                    width: 100%
                    padding-left: 50px
                    color: #999999
                    font-size: r(16px)
                    font-weight: 400
                    letter-spacing: 0.16px
                    border: 0
                    transition: .3s all  ease-in-out
                    background: transparent
                    border-radius: 5px
                    border: 1px solid #e1e1e1
                    &:active
                        border: thin solid $main
                    &:focus
                        border: thin solid $main
                    &::placeholder
                        color: #999999
                input
                    height: 40px
                textarea
                    min-height: 100px
                &.frm-btnwrap
                    margin-top: 20px
