.history-achi
    &.main-section
        @media screen and ( min-width: 1280px )
            padding: 90px 0px
            padding-bottom: 0px
    .des
        max-width: 736px
        margin: 0 auto
        text-align: center
        margin-top: 20px
    .history-top .swiper-slide
        min-height: 446px
        padding-top: 45px
        @media screen and ( max-width: 768px )
            padding-top: 0px
        .title-small
            font-size: r(24px)
            letter-spacing: 0.48px
            color: $gray
            margin-top: 16px
            text-transform: uppercase
        .des
            margin-top: 12px
            p
                color: $gray
                font-size: r(20px)
                font-weight: 400
                text-align: left
                line-height: 1.2
    .wrap-slide
        margin-top: 46px
    .wrap-slide-thums
        position: relative
        margin-bottom: 25px
        .wrap-button
            display: flex
            align-items: center
            justify-content: center
            @media screen and ( max-width: 768px )
                margin-top: 25px

            .button-next, .button-prev
                position: absolute
                top: 25%
                transform: translateY(-50%)
                @media screen and ( max-width: 768px )
                    position: static
                    transform: translateY(0)
            .button-next
                left: 100%
                @media screen and ( max-width: 768px )
                    margin-left: 15px
                img
                    width: 24px
                    min-width: 24px
            .button-prev
                right: 100%
                @media screen and ( max-width: 768px )
                    margin-right: 15px

                img
                    width: 24px
                    min-width: 24px
    .history-thumbs
        border-bottom: 1px solid #e1e1e1
        .swiper-slide
            padding-bottom: 58px

        .swiper-slide-thumb-active
            border-bottom: 3px solid $main
            .year
                background-color: $main
                color: white
                &::before
                    position: absolute
                    bottom: -27px
                    transform: translateX(-50%)
                    left: 58%
                    background-image: url(../img/history-achi/dropshadow.png)
                    width: 121px
                    height: 15px
                    content: ""
        .year
            width: 60px
            height: 60px
            cursor: pointer
            background-color: $bg_color
            display: flex
            align-items: center
            justify-content: center
            color: #999999
            font-size: r(18px)
            font-weight: 600
            letter-spacing: 0.36px
            border-radius: 50%
            margin: 0 auto
            position: relative
