.sabeco-social-responsibilities-1
	padding: 35px 0
	.menu-list
		padding-top: 30px 
		display: flex
		align-items: center
		justify-content: space-around
		flex-wrap: wrap 
		li 
			display: flex
			justify-content: center
			align-items: center
			margin-bottom: 20px
			flex: 0 0 100%
			max-width: 100%
			width: 100%
			a 
				font-size: 16px
				font-weight: 400
				color: $gray 
				transition: .2s ease-in-out all
				text-transform: uppercase
				position: relative
				&::before
					content: ''
					position: absolute
					top: 100%
					left: 0
					background: $main 
					width: 0
					height: 2px
				&:hover 
					text-shadow: 0.7px 0 0 rgba($gray, 1)
					&::before
						width: 100%
			&.active
				a 
					&::before
						width: 100%
		@media(min-width: 768px)
			justify-content: center 
			li 
				width: 100%
				flex: 0 0 50%
				max-width: 50% 
				// margin: 0 
				position: relative
				padding-left: 15px
				padding-right: 15px
				&::before
					content: ''
					position: absolute
					top: 50%
					right: 0
					transform: translateY(-50%)
					background: rgba($main, 0.28)
					width: 1px 
					height: 40px
				&:last-child
					&::before
						display: none
				a 
					font-size: r(22px)
		@media(min-width: 1024px)
			padding-top: 35px
			li
				&::before
					height: 50px
		@media(min-width: 1280px)
			li 
				// margin: 0 10%
				&:first-child
					margin-left: 0
				&:last-child
					margin-right: 0
				&::before
					height: 57px
	@media(min-width: 1280px)
		padding-bottom: 7px
.sabeco-social-responsibilities-2
	padding: 35px 0
	.box-image
		max-width: 458px
	.image
		+img-scale(482/458)
	.box-content 
		margin-top: 30px
		.main-title-child
			line-height: 1.1
			font-weight: 700
			@media(max-width: 576px)
				text-align: center
		.full-content 
			font-size: r(20px)
			font-weight: 400
			line-height: 1.3
			max-height: 352px
			overflow-y: auto 
			padding-right: 10px
			position: relative
			margin-right: 1px
			margin-top: 20px
			&::-webkit-scrollbar
				width: 3px
			&::-webkit-scrollbar-track
				background: #e1e1e1
			&::-webkit-scrollbar-thumb
				background: $main
			&.change-survive
				font-size: r(18px)
				line-height: 1.3
				> * 
					font-size: r(18px)
					line-height: 1.3
		@media(min-width: 768px)
			margin-top: 20px
			margin-left: 30px
			.full-content
				padding-right: 25px
				margin-top: 25px
	.social-responsibilities-item
		+ .social-responsibilities-item
			margin-top: 30px
			@media(min-width: 768px)
				margin-top: 40px
				&:nth-child(2n)
					flex-direction: row-reverse
					.box-image
						margin-right: 0
						margin-left: auto 
					.box-content 
						margin-left: 0
						margin-right: 30px
			@media(min-width: 1024px)
				margin-top: 50px
			@media(min-width: 1280px)
				margin-top: 70px
			@media(min-width: 1440px)
				margin-top: 100px
	@media(min-width: 1280px)
		padding-top: 40px
		padding-bottom: 50px
