.pages-banner
    min-height: 418px
    overflow: hidden
    @media screen and ( max-width: 1024px )
        height: 60vh
        min-height: 60vh
    .swiper-container
        display: flex
        justify-content: center
        align-items: center
    .swiper-slide
        position: relative
        // display: flex
        // justify-content: center
        // align-items: center
        .img
            height: auto
            img
                width: 100%
                height: 100%
                object-fit: cover
            @media screen and ( max-width: 1024px )
                height: 60vh
        .content
            position: absolute
            text-shadow: 0 10px 95px rgba(26, 26, 26, 0.17)
            color: $white
            font-size: r(64px)
            font-weight: 900
            text-transform: uppercase
            width: 100%
            letter-spacing: 0.83px
            padding: 0px 15px
            line-height: 1.1!important
            text-align: center
            left: 50%
            top: 50%
            width: 100%
            transform: translate(-50%,-50%)
            line-height: 59.1px
            span
                display: block
    .pages-banner-arrow
        position: absolute
        bottom: 30px
        z-index: 10
        width: 44px
        height: 24px
        animation: slideDown 1.5s infinite alternate
        cursor: pointer
        @media screen and ( max-width: 768px )
            width: 39px
            height: 19
            bottom: 25px
            img
                object-fit: cover
        @media screen and ( max-width: 375.98px )
            width: 24px
            height: 19px
    @media screen and ( max-width: 768px )
        height: 55vh
        min-height: 55vh
        .content
            font-size: 40px !important
        .swiper-slide
            .img
                height: 55vh
    @media screen and ( max-width: 576px )
        height: 50vh
        min-height: 50vh
        .content
            font-size: 30px !important
        .swiper-slide
            .img
                height: 50vh
    @media screen and ( max-width: 375.98px )
        height: 45vh
        min-height: 45vh
        .content
            font-size: 25px !important
        .swiper-slide
            .img
                height: 45vh
@keyframes slideDown
    0%
        transform: translateY(-5px)
    80%
        transform: translateY(5px)
    100%
        transform: translateY(5px)
        opacity: 0.5
