.pages-sustainability-sec-1
    &.main-section
        @media screen and ( min-width: 1280px )
            padding-top: 90px
            padding-bottom: 55px
    .top-content
        max-width: 800px
        text-align: center
        color: $gray
        font-size: r(20px)
        font-weight: 400
        letter-spacing: 0.4px
        margin: 0 auto
.pages-sustainability-sec-2
    border-top: thin solid $main
    &.main-section
        @media screen and ( min-width: 1280px )
            padding-top: 65px
            padding-bottom: 80px
    .des-icon
        padding-top: 65px
        @media screen and ( max-width: 1280px )
            .col-xl-2
                margin-bottom: 20px
        @media screen and ( max-width: 576px )
            padding-top: 50px
        .des-icon-list
            display: flex
            justify-content: center
            align-items: center
            flex-direction: column
            width: 235px
            height: 235px
            background-color: $main
            @media screen and ( max-width: 1280px )
                width: auto
                height: 100%
                padding: 33px 40px
                margin-bottom:
            .icon
                margin-bottom: 25px
            .content
                color: $white
                font-size: r(22px)
                font-weight: 700
                text-align: center
                text-transform: uppercase
                letter-spacing: 0.44px
                line-height: 1.3
                span
                    display: block
        @media screen and ( max-width: 375.98px )
            .des-icon-list
                padding: 20px
                .icon
                    text-align: center
                    margin-bottom: 15px
                    img
                        width: 70.5%
                .content
                    font-size: r(17px)
    .des-box
        padding-top: 90px
        display: flex
        justify-content: center
        align-items: center
        @media screen and ( max-width: 1024px )
            flex-direction: column
            padding-top: 35px
            .content-wrapper,.img
                flex: 1
            .content-wrapper
                padding: 30px !important
                order: 2
            .img
                order: 1
                width: 100%
                height: 100%
        @media screen and ( max-width: 1024px )
            flex-direction: column
            padding-top: 35px
            .content-wrapper,.img
                flex: 1
            .content-wrapper
                padding: 30px !important
                order: 2
            .img
                order: 1
                width: 100%
                min-height: 219px !important
                max-height: 219px !important
        .content-wrapper
            display: flex
            flex-direction: column
            padding: 40px 80px
            padding-right: 65px
            background-color: #f8f8f8
            height: 482px
            .block-title
                margin-bottom: 15px
            .content
                color: $gray
                font-size: r(20px)
                font-weight: 400
                letter-spacing: 0.4px
                line-height: 1.2
            .button
                @media screen and ( min-width: 1024px )
                    margin-top: 55px
        .img
            flex: 0 0 458px
            min-height: 482px
            height: 482px
            img
                width: 100%
                height: 100%
                object-fit: cover

.pages-sustainability-sec-3
    min-height: 580px
    display: flex
    justify-content: flex-end
    align-items: center
    @media screen and ( max-width: 575px )
        .sec-3
            max-width: none !important
            width: 100% !important
            .button
                margin-top: 20px
    @media screen and ( max-width: 375.98px )
        align-items: flex-start
        .sec-3
            margin-top: 10px
            .main-title
                margin-bottom: 20px !important
            .content
                text-align: center !important
                height: 78px
                overflow: auto
                padding: 0px 3px
                &::-webkit-scrollbar-track
                    box-shadow: inset 0 0 6px rgba(255, 255, 255, 0)
                    background-color: $main
                &::-webkit-scrollbar
                    width: 2px
                    height: 2px
                &::-webkit-scrollbar-thumb
                    border-radius: 5px
                    box-shadow: inset 0 0 6px rgba(255, 255, 255, 0)
                    background-color: $white
            .button
                margin-top: 30px
                width: 100%
    .container
        float: right
        width: 100%
    .sec-3
        max-width: 408px
        margin-left: auto
        .main-title
            margin-bottom: 11px
        .content
            color: $white
            font-size: r(20px)
            font-weight: 400
            text-align: left
            letter-spacing: 0.4px
            line-height: 1.2
        .button
            @media screen and ( min-width: 1024px )
                margin-top: 25px
