.mission-body
    margin: 0 auto!important
    .fancybox-button
        background-color: $main
        width: 40px
        height: 40px
        opacity: 1
        color: white!important
        padding: 3px!important
.mission
    .wrap-ms-2
        .box-text
            padding-right: 36px!important
            @media screen and ( max-width: 575px )
                padding-right: 20px!important
    .wrap-mission-1
        margin-top: 100px
        display: flex
        align-content: center
        flex-wrap: wrap
        @media screen and ( max-width: 992px )
            margin-top: 50px
        .box-text
            flex: 0 0 545px
            min-height: 482px
            background-color: #f8f8f8
            padding-left: 86px
            padding-top: 102px
            padding-right: 72px
            padding-bottom: 61px
            @media screen and ( max-width: 1024px )
                flex: 0 0 50%
                padding: 30px
                padding-top: 60px
            @media screen and ( max-width: 768px )
                flex: 0 0 100%
                padding: 50px

                height: auto
                order: 2
                min-height: auto
            @media screen and ( max-width: 575px )
                padding-left: 20px
                padding-right: 20px
                padding-top: 38px
                padding-bottom: 30px
                display: flex
                align-items: center
                justify-content: center
                flex-direction: column

            &.pt-50
                padding-top: 50px!important
            .main-title
                margin-bottom: 23px
                font-size: r(45px)
                @media screen and ( max-width: 575px  )
                    font-size: 38px

            .des
                // max-width: 360px
                p
                    margin-bottom: 20px
                @media screen and ( max-width: 1024px )
                    max-width: 100%
                @media screen and ( max-width: 575px )
                    text-align: center

                &.scroll
                    overflow-y: auto
                    padding-right: 30px
                    height: 307px
                    /* width */
                    &::-webkit-scrollbar
                        width: 3px
                    &::-webkit-scrollbar-track
                        background: #e1e1e1
                    &::-webkit-scrollbar-thumb
                        background: #d7282f
                    @media screen and ( max-width: 575px )
                        padding-right: 0
                    @media screen and ( max-width: 400px )
                        padding-right: 20px


        .box-img
            flex: 1
            min-height: 482px
            @media screen and ( max-width: 1024px )
                flex: 0 0 50%

            @media screen and ( max-width: 768px )
                flex: 0 0 100%
                order: 1
                height: 100%
                min-height: auto
            @media screen and ( max-width: 575px )
                height: auto

            .img
                width: auto
                height: 100%
                img
                    width: 100%
                    height: 100%
                    object-fit: cover
.mission-2
    min-height: 712px
    margin-top: 50px
    padding-top: 50px
    .main-title
        @media screen and ( max-width: 575px )
            font-size: 38px
    @media screen and ( max-width: 992px )
        margin-top: 0px


    .wrap-mi
        max-width: 745px
        margin: 0 auto
        margin-top: 35px
        .col-md-2
            @media screen and ( min-width: 768px )
                flex: 0 0 33.33%
                max-width: 33.33%
        .box-item
            width: auto
            height: 235px
            background-color: $main
            margin-bottom: 20px
            display: flex
            align-items: center
            flex-direction: column
            cursor: pointer
            padding-top: 42px
            .img
                margin-bottom: 20px
                height: 87px
                display: flex
                align-items: center
            .title
                color: $white
                font-size: r(22px)
                line-height: 1.1
                font-weight: 700
                text-align: center
                text-transform: uppercase
                letter-spacing: 0.44px
.mission-popup
    max-width: 786px!important
    .sabeco-title
        margin-bottom: 20px


