.pages-general-meetings
    border-bottom: thin solid $main
    &.main-section
        @media screen and ( min-width: 1280px )
            padding-top: 65px
            padding-bottom: 75px
        @media screen and ( min-width: 475px )
            padding-top: 60px
    @media screen and ( min-width:1280px  )
        .left-side
            padding-right: 0px
            margin-right: -10px
        .right-side
            padding: 0px
    .left-side
        .left-side-container
            @media screen and ( min-width: 1280px )
                max-width: 515px
        .block-title
            letter-spacing: 0px
            span
                display: block
        .year
            display: flex
            justify-content: flex-end
            align-items: center
            margin-top: -15px
            @media screen and ( max-width: 375.98px )
                p
                    font-size: r(18px)
            @media screen and ( max-width: 576px )
                justify-content: flex-start
            p
                color: $black
                font-size: r(16px)
                font-weight: 400
                text-transform: uppercase
                letter-spacing: 0.32px
            select
                color: $black
                font-size: r(17px)
                font-weight: 700
                min-width: 58px
                text-transform: uppercase
                letter-spacing: 0.34px
                margin-left: 20px
                border: 0
                background-image: url(../img/annual/select_arrow.png)
                background-repeat: no-repeat
                background-position: 44px 9px
                appearance: none

        .meetings-list
            .meeting-item
                padding: 30px 0px
                border-bottom: thin solid $main
                &:last-child
                    margin-bottom: 40px
                    @media screen and ( max-width: 1024px )
                        margin-bottom: 30px
            .title
                margin-top: 10px
                color: $gray
                font-size: r(20px)
                font-weight: 400
                letter-spacing: 0.4px
        .button
            padding-top: 5px
            a.btn-view-more
    .right-side
        @media screen and ( max-width: 768px )
            padding-top: 40px
            padding-bottom: 40px
        .upcoming-wrapper
            .block-title
                padding-top: 0px

        .block-title
            padding-top: 60px
            @media screen and ( max-width: 768px )
                padding-top: 30px
            span
                display: block
        .contact-investors
            background-color: $bg_color
            border-left: 7px solid $main
            padding: 30px 10px 36px 25px
            .title
                color: $main
                font-size: r(20px)
                font-weight: 900
                text-transform: uppercase
                letter-spacing: 0.16px
                line-height: 1.25
            .address
                margin-top: 20px
                .item
                    display: flex
                    align-items: flex-start
                    img
                        margin-top: 3px
                    p
                        margin-left: 15px
                        color: $gray
                        font-size: r(16px)
                        font-weight: 400
                        letter-spacing: 0.26px
                        line-height: 1.1
                .item + .item
                    margin-top: 15px
